/*Navbar Styles*/
.navbar {
    padding: 3rem 0 1rem;
    position: relative;
    box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.1);
}

.navbar .cannabis-img {
    position: absolute;
    top: -1.5rem;
    left: -1.5rem;
    z-index: -1;
    height: 10rem;
}

.btn-toggle {
    font-size: 3.5rem;
    cursor: pointer;
    color: #242e42;
    display: none;
}

.navbar-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar-wrapper .logo img {
    height: 6rem;
}

.navbar-wrapper .nav__list {
    display: flex;
    align-items: center;
}
.navbar-wrapper .nav__list-item:not(:last-child) {
    margin-right: 2rem;
}

.navbar-wrapper .nav__list-item {
    position: relative;
}

.navbar-wrapper .nav__list-item::before {
    position: absolute;
    content: "";
    bottom: -0.5rem;
    right: 0;
    width: 0;
    height: 0.75rem;
    border-radius: 1rem;
    background-color: #61d273;
    transition: all 0.5s cubic-bezier(0.165, 0.84, 0.44, 1);
}
.navbar-wrapper .nav__list-item.active::before,
.navbar-wrapper .nav__list-item:hover:not(.active):before {
    width: 2rem;
}

.navbar-wrapper .nav__list-link {
    font-size: 1.5rem;
    text-transform: uppercase;
    color: #242e42;
    font-weight: 400;
}

.navbar-wrapper .nav-link-icon {
    margin: 0 2rem;
}

.navbar-wrapper .nav-link-icon img {
    height: 2rem;
    transform: translateY(-0.4rem);
}

.navbar-wrapper .nav-link-icon .nav__list-link {
    display: flex;
    align-items: center;
}
.navbar-wrapper .nav-link-icon:last-child img {
    margin-right: 0.5rem;
}
@media screen and (max-width: 1075px) {
    .navbar-wrapper .nav__list-link {
        font-size: 1.4rem;
    }
    .navbar-wrapper .nav__list-item:not(:last-child) {
        margin-right: 1rem;
    }
    .navbar-wrapper .nav-link-icon {
        margin: 0 1rem;
    }
}
@media screen and (max-width: 910px) {
    .btn-toggle {
        font-size: 3.5rem;
        cursor: pointer;
        color: #242e42;
        display: block;
    }
    .nav__list {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        padding: 4rem 0;
        background: #fff;
        z-index: 10;
        box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.2);
        transform: translateY(-105%);
        border-top: 0.4rem solid #61d273;
        transition: all 0.5s cubic-bezier(0.19, 1, 0.22, 1);
    }
    .nav__list.show {
        transform: translateY(10rem);
    }
    .navbar-wrapper .nav__list-item {
        display: block;
    }
    .navbar-wrapper .nav__list-item:not(:last-child) {
        margin-right: 0;
        margin-bottom: 2rem;
    }
    .navbar-wrapper .nav__list-link {
        font-size: 1.8rem;
    }
    .navbar .cannabis-img {
        top: -4.5rem;
        left: -4.5rem;
    }
}

