    padding: 0.5rem 2rem;
    border: 0.15rem solid #242e42;
    text-transform: uppercase;
    transition: all 0.4s cubic-bezier(0.55, 0.055, 0.675, 0.19);
    margin-left: 1rem;
}
.footer-form .form-group button:hover {
    background: #242e42;
    color: #fff;
}

.footer-social {
    color: #242e42;
    font-size: 2rem;
    margin-left: 1rem;
}
@media screen and (max-width: 650px) {
    .footer-wrapper {
        flex-direction: column;
    }
    .footer-item:nth-child(1) {
        margin-bottom: 4rem;
    }
}

.slick-arrow {
    width: 4rem !important;
    height: 4rem !important;
    background: #61d273 !important;
    border-radius: 50%;
}

.slick-next::before,
.slick-prev::before {
    font-family: "slick";
    line-height: 1;
    width: 4rem;
    height: 4rem;
    opacity: 1 !important;
    border-radius: 50%;
}

.slick-prev::before {
    content: url(data:image/png;base64,) !important;
}

.slick-next::before {
    content: url(data:image/png;base64,) !important;
}

.slick-next {
    right: -6rem !important;
}

.slick-prev {
    left: -6rem !important;
}
@media screen and (max-width: 450px) {
    .slick-next {
        right: -3rem !important;
    }
    .slick-prev {
        left: -3rem !important;
        z-index: 210;
    }

}

